import React, { useEffect } from "react";
import { SignupForm } from "./SignupForm";
import { LoginForm } from "./LoginForm";
import $ from "jquery";

export const LoginPopup = (props) => {
    useEffect(() => {
        $("#LoginPopup").ready(() => {
            const PopupSignupButton = document.getElementById("popup-signUp");
            const PopupSigninButton = document.getElementById("popup-logIn");
            const Popupcontainer = document.getElementById("popup-container");
            PopupSigninButton &&
                PopupSignupButton.addEventListener("click", () => {
                    Popupcontainer.classList.add("right-panel-active");
                });

            PopupSignupButton &&
                PopupSigninButton.addEventListener("click", () => {
                    Popupcontainer.classList.remove("right-panel-active");
                });

            const ActivePopup = () => {
                $(".popup").css("transform", "scale(1)");
                $(".main").addClass("blur");
            };
            $("#login").click((e) => {
                if ($(".popup-container").hasClass("right-panel-active")) {
                    $(".popup-container").removeClass("right-panel-active");
                }
                ActivePopup();
            });
            $(document).on("click", "#login2", () => {
                if ($(".popup-container").hasClass("right-panel-active")) {
                    $(".popup-container").removeClass("right-panel-active");
                }
                ActivePopup();
            });
            $("#signup").on("click", () => {
                if (!$(".popup-container").hasClass("right-panel-active")) {
                    $(".popup-container").addClass("right-panel-active");
                }
                ActivePopup();
            });
            $(document).on("click", "#signup2", () => {
                if (!$(".popup-container").hasClass("right-panel-active")) {
                    $(".popup-container").addClass("right-panel-active");
                }
                ActivePopup();
            });

            $("#signup2").on("click", () => {
                if (!$(".popup-container").hasClass("right-panel-active")) {
                    $(".popup-container").addClass("right-panel-active");
                }
                ActivePopup();
            });
            $("#close-popup").on("click", () => {
                $(".popup").css("transform", "scale(0)");
                $(".main").removeClass("blur");
            });

            $(".overlay").css("width", $(".popup-container").css("width"));
            $(".overlay").on("resize", () => {
                this.css("width", $(".popup-container").css("width"));
            });
        });

        return () => {};
    }, []);
    return (
        <div className="popup" id="LoginPopup">
            <div className="popup-container" id="popup-container">
                <button id="close-popup">
                    <i className='fas fa-times'></i>
                </button>
                <div className="signup-form form-container col-12 col-lg-7">
                    <SignupForm />
                </div>
                <div className="signin-form form-container col-12 col-lg-7">
                    <LoginForm />
                </div>
                <div className="overlay-container d-none d-lg-block">
                    <div className="overlay">
                        <div className="overlay-panel overlay-left">
                            <h1>Chào mừng trở lại!</h1>
                            <p>Để kết nối với chúng tôi, xin vui lòng đăng nhập với thông tin cá nhân của bạn</p>
                            <button className="ghost" id="popup-logIn">
                                Đăng nhập
                            </button>
                        </div>
                        <div className="overlay-panel overlay-right">
                            <h1>Xin chào!</h1>
                            <p>Nhập đầy đủ thông tin cá nhân và bắt đầu hành trình mới nào</p>
                            <button className="ghost" id="popup-signUp">
                                Đăng ký
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default LoginPopup;
